import React, { useState, useEffect } from 'react';
import { Collapse, Drawer, Button, Tooltip, Form, Input, Switch, Select, message, Row, Col } from 'antd';
import { Icon, Selector } from "@/components";
import Apis from "@/apis";
const { Panel } = Collapse;
const labelCol = { span: 6 }
const comOptions = [
  { label: '输入框', value: 'Input' },
  { label: '下拉菜单', value: 'Select' },
  { label: '数值框', value: 'InputNumber' },
  { label: '日期', value: 'DatePicker' },
  { label: '开关按钮', value: 'Switch' },
]
const typeOptions = [
  { label: '字符串', value: 'string' },
  { label: '数值', value: 'number' },
  { label: '布尔值', value: 'boolean' },
  { label: '网址', value: 'url' },
  { label: '邮箱地址', value: 'email' },
]

const AppView = (props, ref) => {
  const [items, setItems] = useState([]);
  const { comName, viewData, onUpdate, onClose, ...otherProps } = props

  useEffect(() => {
    if (comName && viewData && viewData.components) {
      let component = viewData.components.find(item => item.__component === comName)
      if (component && Array.isArray(component.items)) {
        let _items = component.items.map(item => {
          if (item.render && item.render.id) item.renderId = item.render.id
          if (component.rules) {
            let { _id, id, ...hasOne } = component.rules.find(rule => rule.name === item.name) || {}
            return { ...item, ...hasOne }
          } else {
            return { ...item }
          }
        })
        setItems(_items)
      }
    }
  }, [comName, viewData])

  const handleClick = (index, type, e) => {
    e.stopPropagation();
    let _items = items.splice(index, 1)
    if (type === 'up') {
      items.splice(index - 1 < 0 ? 0 : (index - 1), 0, ..._items)
    } else if (type === 'down') {
      items.splice(index + 1 > items.length ? items.length : (index + 1), 0, ..._items)
    }
    setItems(Array.from(items))
  }

  const genExtra = (index, items) => {
    return <span>
      {index ? <Tooltip placement="bottom" title={'上移'} key="0">
        <Button type="link" size="small" icon={<Icon iconName="Up" />} onClick={e => handleClick(index, 'up', e)}></Button>
      </Tooltip> : null}
      {index < items.length - 1 ? <Tooltip placement="bottom" title={'下移'} key="1">
        <Button type="link" size="small" icon={<Icon iconName="Down" />} onClick={e => handleClick(index, 'down', e)}></Button>
      </Tooltip> : null}
    </span>
  }

  const handleChange = (index, fieldName, e) => {
    let item = items[index]
    if (item) item[fieldName] = e ? (e.target ? e.target.value : e) : null
    setItems(Array.from(items))
  }

  const handleSave = () => {
    if (comName && viewData) {
      let component = viewData.components.find(item => item.__component === comName)
      if (component) {
        component.items = items.map(item => {
          let { type, required, message, ...data } = item
          if (data.selectOptions && data.selectOptions.id) data.selectOptions = data.selectOptions.id
          if (item.render && item.renderId) item.render = item.renderId
          let rule = component.rules.find(rul => rul.name === item.name)
          if (rule) {
            Object.assign(rule, { type, required })
          } else {
            if (type) component.rules.push({ type, required, name: item.name, message })
          }
          return data
        })
        Apis.updateViewData(viewData).then(res => {
          if (res) {
            message.success("保存成功!");
            if (typeof onUpdate === 'function') onUpdate()
          } else {
            message.error("保存失败!");
          }
        })
      }
    }
  }

  const genItem = items => {
    return items.map((item, index) => (
      <Panel header={item.label} extra={genExtra(index, items)} key={index}>
        <Form.Item labelCol={labelCol} label="标题">
          <Input onChange={handleChange.bind(this, index, 'label')} value={item.label || ''} />
        </Form.Item>
        <Form.Item labelCol={labelCol} label="输入组件">
          <Select
            options={comOptions}
            onChange={handleChange.bind(this, index, 'component')}
            value={item.component}
          />
        </Form.Item>
        {
          item.component === 'InputNumber' ? (
            <Form.Item labelCol={labelCol} label="单位">
              <Input onChange={handleChange.bind(this, index, 'unit')} value={item.unit || ''} />
            </Form.Item>
          ) : null
        }
        {
          item.component === 'Select' ?
            <Form.Item labelCol={labelCol} label="数据字典">
              <Selector
                api="dictionarys"
                params={{ _limit: 1000 }}
                value={item.selectOptions ? item.selectOptions._id : ''}
                titlepropname="title"
                valuepropname="_id"
                onChange={handleChange.bind(this, index, 'selectOptions')}
              />
            </Form.Item> : null
        }
        <Form.Item labelCol={labelCol} label="描述信息">
          <Input onChange={handleChange.bind(this, index, 'help')} value={item.help || ''} />
        </Form.Item>
        <Row>
          <Col span={12}>
            <Form.Item labelCol={{ span: 12 }} label="清除值">
              <Switch onChange={handleChange.bind(this, index, 'resetEnable')}
                checkedChildren="是" unCheckedChildren="否"
                defaultChecked={item.resetEnable} />
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item labelCol={{ span: 12 }} label="必填">
              <Switch onChange={handleChange.bind(this, index, 'required')}
                checkedChildren="是" unCheckedChildren="否"
                defaultChecked={item.required} />
            </Form.Item>
          </Col>
        </Row>
        <Form.Item labelCol={labelCol} label="校验规则">
          <Select
            options={typeOptions}
            onChange={handleChange.bind(this, index, 'type')}
            value={item.type}
          />
        </Form.Item>
        <Form.Item labelCol={labelCol} label="校验提示">
          <Input
            onChange={handleChange.bind(this, index, 'message')}
            value={item.message || ''}
          />
        </Form.Item>
        <Form.Item labelCol={labelCol} label="表单函数">
          <Selector
            api="customizeFuns"
            params={{ _limit: 1000, type_ne: 'table_column' }}
            titlepropname="title"
            valuepropname="id"
            grouppropname="type"
            showSearch
            searchname="title"
            filterOption={(input, option) =>
              option.children ? option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0 : false
            }
            allowClear
            onChange={handleChange.bind(this, index, 'renderId')}
            value={item.renderId}
          />
        </Form.Item>
      </Panel>
    ))
  }

  return (
    <Drawer
      title={'标题'}
      placement="right"
      closable={true}
      width={350}
      zIndex={1000}
      bodyStyle={{ padding: 0 }}
      className="fieldSetting"
      onClose={onClose}
      footer={
        <div style={{ textAlign: 'center', }}>
          <Button onClick={handleSave} type="primary" style={{ marginRight: 8 }}>
            保存
          </Button>
          <Button onClick={e => onClose ? onClose() : null}>
            取消
          </Button>
        </div>
      }
      {...otherProps}
    >
      <Collapse
        bordered={false}
        accordion={true}
        defaultActiveKey={['']}
      >
        {genItem(items)}
      </Collapse>
    </Drawer>
  );
}
export default AppView